.menu-btn span {
  width: 30px;
  height: 3px;
  background-color: #fff;
  transition: all .5s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.menu-btn.active span {
  background-color: #262626;
}

.menu-btn span:nth-of-type(2) {
  top: calc(40% - 5px);
}

.menu-btn span:nth-of-type(3), .menu-btn span:nth-of-type(4) {
  top: calc(60% + 5px);
